import React, { useState, useEffect } from 'react';
import errorBoundary from '@src/components/errorBoundary';
import { getTimelineList } from '@src/public/services';
import { inFlowIframeResize, inFlowIframeJump } from '@src/public/utils';
import { isGrounds } from '@src/public/utils/app';
import { getQuery } from '@src/public/utils/url';
import EventList from '@src/mobile/components/EventList';
import { EVENT_LIST_INFLOW } from '@src/public/constants';
import { dispatchPage, linkBase } from '@src/mobile/components/Link';
import styles from './index.css';

const Layout = () => {
    const [content, setContent] = useState(null);
    const [fold, setFold] = useState(false);
    const [id, setId] = useState('');

    const initData = async () => {
        try {
            // id
            const id = getQuery(window.location.href, 'id');
            // 
            const size = getQuery(window.location.href, 'size');
            //   0 
            const party = getQuery(window.location.href, 'party');
            //  0  1 
            const highlights = getQuery(window.location.href, 'highlights');
            //  asc  desc 
            const order = getQuery(window.location.href, 'order');
            //  0  1 
            const fold = getQuery(window.location.href, 'fold');

            setFold(fold === '1');
            setId(id);

            const res = await getTimelineList({ id, page: 1, size, party, highlights, order });
            const { nodes, parties, timeline } = res;

            setContent({ nodes, parties, timeline });
        } catch (e) {
            console.error(e);
        }
    };

    useEffect(() => {
        initData();
    }, []);

    useEffect(() => {
        // post
        inFlowIframeResize(document.documentElement.offsetHeight || document.body.offsetHeight);

        // postMessage
        let count = 0;

        // 
        const timer = setInterval(() => {
            if (count >= 20) {
                clearInterval(timer);
            }
            const docHeight = document.documentElement.offsetHeight || document.body.offsetHeight;

            inFlowIframeResize(docHeight);
            count += 1;
        }, 1000);

        return () => {
            clearInterval(timer);
        };
    }, []);

    return (
        <div className={styles.layout}>
            {content ? (
                <>
                    <div className={styles.list}>
                        <EventList content={content} allFold={fold} showType={EVENT_LIST_INFLOW} />
                    </div>
                    <div
                        className={styles.more}
                        onClick={() => {
                            const to = `${linkBase}/loc/timeline/event/${id}`;

                            if (isGrounds) {
                                dispatchPage({ to });
                            } else {
                                inFlowIframeJump(to);
                            }
                        }}>
                        <div className={styles.moreText}></div>
                        <div className={styles.moreArrow} />
                    </div>
                </>
            ) : (
                <div className={styles.loading} />
            )}
        </div>
    );
};

export default errorBoundary(Layout);
